.error-container {
  padding: 1rem;
  margin: 1rem 0;
  border-top: 2px solid rgba(243, 16, 0, 0.664);
  border-bottom: 2px solid rgba(243, 16, 0, 0.664);
  color: black;
  background-color: rgba(244, 67, 54, 0.5);
  text-align: center;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
}

.error-container .error-text {
  font-size: 1.3rem;
}

.error-container .error-button {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  margin-left: auto;
  margin-right: auto;
  border-radius: 7px;
  padding: 0.5rem 1rem;
  margin-top: 0.5rem;
  border: 2px solid #000000;
}
